import {useState, useEffect, useContext} from 'react';
import {ethers} from 'ethers';

import "./LoadingBar.css"; 
import CONTRACT_INFO from '../ContractInfo';

import bouncingWolf from '../assets/bouncing-wolf.gif';



const LoadingBar = () => { 

  const [tokensInStakingContract, setTokenInStakingContract] = useState()
  const [percentageStaked, setPercentageStaked] = useState()
  const [absoluteStaked, setAbsoluteStaked] = useState()

  const [moonrunnerOffset, setMoonrunnerOffset] = useState()

  const MAX_SUPPLY = 10000;

  async function getTokens() {
    const provider = new ethers.providers.Web3Provider(window.ethereum, "any");

    const erc721Contract = new ethers.Contract(CONTRACT_INFO.moonrunners.contract, CONTRACT_INFO.moonrunners.abi, provider)
    const stakingContract = new ethers.Contract(CONTRACT_INFO.trophies.contract, CONTRACT_INFO.trophies.abi, provider);

    const balanceOfTokensStaked = await erc721Contract.balanceOf(CONTRACT_INFO.trophies.contract)  

    const numberOfStakedTokens = balanceOfTokensStaked.toNumber()

    setAbsoluteStaked(numberOfStakedTokens)

    const percentageStaked = ((numberOfStakedTokens/MAX_SUPPLY) * 100)

    const percentageStakedString = percentageStaked + '%';

    const moonrunnnerPercentageOffset = percentageStaked-10 +'%';

    setMoonrunnerOffset(moonrunnnerPercentageOffset);

    setPercentageStaked(percentageStakedString)
   
}

useEffect(() => {
  getTokens();
},[]); 

  return (
    <>
    <div className='column'>
      <div className="loading-bar">
        <div className="loading-bar-progress"
          style={{
            position: "absolute",
            width: percentageStaked,
            height: "100%",          
            }}> 
          </div >
          <div className="loading-bar-moonrunner"
            style={{
              marginLeft: moonrunnerOffset,
              position: "relative",
              // marginLeft: calc(percentageStaked - "10%"),
              width: "20%",
              height: "100%",
              textAlign: "center",
              display: "flex",
              justifyContent: "center",
              alignItems: "center"
            }}     
          >
            <img
              src={bouncingWolf}
              className="loading-bar-img"
              >
            </img>
          </div>
      </div>


    </div>
    <div> 

    </div>
    <div className='columns is-centered is-vcentered'>
    <div className='number-staked'>
          {absoluteStaked} / {MAX_SUPPLY} staked

      </div>

    </div>
   
    </>
  );
};

export default LoadingBar; 