import {useState, useEffect, useContext} from 'react';
import {StakedTokensContext} from './Staking';
import {ethers} from 'ethers';
import './Trophies.css';

import clock from "../assets/Clock.gif";

import trophy1 from '../assets/trophy1.png';
import trophy2 from '../assets/trophy2.png';
import trophy3 from '../assets/trophy3.png';
import trophy4 from '../assets/trophy4.png';


import CONTRACT_INFO from '../ContractInfo';

const Trophies = () => {

    const userStakedTokens = useContext(StakedTokensContext);

    const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        
    const signer = provider.getSigner();

    const stakingContract = new ethers.Contract(CONTRACT_INFO.trophies.contract, CONTRACT_INFO.trophies.abi, provider).connect(signer);

    const [userTrophy, setUserTrophy] = useState(null)
    const [claimTime, setClaimTime] = useState(null)
    const [possibleTrophy, setPossibleTrophy] = useState(null)
    const [trophyIdAvailableToUser, setTrophyIdAvailableToUser] = useState(null)

    async function currentTrophy(){
        if (userStakedTokens >= 25){
            setTrophyIdAvailableToUser(4)
        }
        else if(userStakedTokens >= 10){
            setTrophyIdAvailableToUser(3)
        }
        else if(userStakedTokens >= 5){
            setTrophyIdAvailableToUser(2)
        }
        else if(userStakedTokens >= 1){
            setTrophyIdAvailableToUser(1)
        }
        else{
            setTrophyIdAvailableToUser(0)
        }
    }

    async function checkTrophy(){
        const trophyIds = [4,3,2,1]
        const userTrophyBalance = await stakingContract.balanceOfBatch([signer.getAddress(), signer.getAddress(),signer.getAddress(),signer.getAddress()], trophyIds); 
        const possibleTrophyId = await stakingContract.getPossibleTrophyClaim();

        const trophyIdAvailableToUser = possibleTrophyId.toNumber()

        const trophies = userTrophyBalance.toString();
        const trophyBalanceOfBatch = trophies.split(',');
        
        for(let i = 0; i < trophyBalanceOfBatch.length; i++){
            if(trophyBalanceOfBatch[i] == 1){
                setUserTrophy(trophyIds[i])
                break;
            }
        }

        if(trophyIdAvailableToUser > 0){
            setPossibleTrophy(true)
        }
    }

    async function checkStakedTime(){
        const stake = await stakingContract.getStake(signer.getAddress())

        const stakingPeriodInBigNumber = await stakingContract.stakingPeriod(signer.getAddress())

        const stakedTimestampUnix = stake.timestamp.toNumber();
        const stakingPeriodInSeconds = stakingPeriodInBigNumber.toNumber()

        const today = Math.floor(Date.now() / 1000)
        const diffTime = (((stakedTimestampUnix + stakingPeriodInSeconds) - today))

        const days = Math.floor(diffTime / (24*60*60));
        const daysms = diffTime % (24*60*60);
        const hours = Math.floor(daysms / (60*60));
        const hoursms = diffTime % (60*60);
        const minutes = Math.floor(hoursms / (60));

        if(diffTime >= 0){
            const timeLeft = ' in ' +  days + ' days ' + hours + ' hours'
            setClaimTime(timeLeft)
        }

        if (diffTime >= 0 && days <= 0 ){
            const timeLeft = 'in ' +  hours + ' hours'
            setClaimTime(timeLeft)
        }

        if (diffTime >= 0 && hours <= 0 ){
            const timeLeft = 'in ' + minutes + ' minutes'
            setClaimTime(timeLeft)
        }

        if(diffTime <= 0){
            setClaimTime(null)
        }

    }

    async function claim() {
        const claim = await stakingContract.claim();

        await claim.wait();
        await checkTrophy();
    }

    useEffect(() => {
        if (!userTrophy)
            checkStakedTime()
            checkTrophy();
            currentTrophy();
        },[possibleTrophy, currentTrophy]); 
    
    return(
        <>
            <div className='columns is-centered is-vcentered trophy-time-text'>                
                <div className='column'>
                    <div>Pledged<strong className='trophies-strong'>&nbsp;{userStakedTokens}&nbsp;</strong>moonrunners</div>
                    <h1 className='trophy-text'>&nbsp;
                        <span>
                            {trophyIdAvailableToUser === 1 ? ' Bronze Trophy ' :
                            trophyIdAvailableToUser === 2 ? ' Silver Trophy' :
                            trophyIdAvailableToUser === 3 ? ' Gold Trophy' :
                            trophyIdAvailableToUser === 4 ? ' Diamond Trophy' :
                            'No trophy available '}
                        </span>
                         {trophyIdAvailableToUser > 0 &&
                          <img className='trophy-claim-image'  src={trophyIdAvailableToUser === 1 ? trophy1 :
                            trophyIdAvailableToUser === 2 ? trophy2 :
                            trophyIdAvailableToUser === 3 ? trophy3 :
                            trophyIdAvailableToUser === 4 ? trophy4 :
                            null} alt="trophy-image"
                          />
                        }
                        {claimTime && <span>{claimTime}</span>}
                    </h1>
                </div>
                    <div className='column is-2'>
                        {claimTime && 
                            <img
                                className='clock-img'
                                src={clock} alt="clock"
                            />}
                    </div>
                </div>
                <div className='column is-1'>
            </div>
        </>  
    )

}

export default Trophies

