import {React, useEffect, useState, createContext} from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, Routes, Route } from "react-router-dom";

import 'bulma/css/bulma.min.css';
import './index.css';

import Navbar from "./components/Navbar";
import Staking from "./components/Staking";
import Raffles from "./components/Raffles";
import WeaponBurn from "./components/WeaponBurn";
import Faq from "./components/Faq";
import Roadmap from './components/Roadmap';


export const WalletContext = createContext()

export default function App() {
  const [walletAddress, setWalletAddress] = useState("");

  async function requestAccount(){
    if(window.ethereum){
      try {
        const accounts = await window.ethereum.request({
          method: "eth_requestAccounts",
        });

        setWalletAddress(accounts)

      } catch (error) {
        console.log(error)
      }

    }
    else{
      console.log('metamask not detected')
    }
  }

  useEffect(() => {
    requestAccount();
  },[]); 

  return (
    <>
    
    <div className="container small-screen-div">
          Not compatible with mobile, please log in with a laptop
    </div>
    <div className='root-body'>
    <WalletContext.Provider value={{ address: walletAddress, connect: requestAccount }}>
    <BrowserRouter>
      <Routes>
        <Route path="/" 
        element={
          <>
            <Navbar />
            <Staking />
          </>
        }
        />
        <Route
          path="/weaponBurn"
          element={
            <>
              <Navbar />
              <WeaponBurn />
            </>
          }
        />
        <Route path="/raffles" element={<Raffles />} />
        <Route
          path="/staking"
          element={
            <>
              <Navbar />
              <Staking />
            </>
          }
          />
          <Route
          path="/faq"
          element={
            <>
              <Navbar />
              <Faq />
            </>
          }
          />
          <Route
          path="/roadmap"
          element={
            <>
              <Roadmap />
            </>
          }
          />
          
      </Routes>
    </BrowserRouter>
    </WalletContext.Provider>
    </div>
    
    </>
  );
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<App />);
