import {useState, useEffect, useContext, createContext} from 'react';
import {WalletContext} from '../index';
import {ethers} from 'ethers';
import './Staking.css';
import Trophies from './Trophies';
import LoadingBar from './LoadingBar';
import CONTRACT_INFO from '../ContractInfo';

import connectButton from '../assets/connect-wallet-button.png';
import loadingBar from '../assets/animated-loadingbar.gif';

import stakeButton from '../assets/stake-button.png';
import stakeAllButton from '../assets/stake-all-button.png';
import unstakeButton from '../assets/unstake-button.png';
import unstakeAllButton from '../assets/unstake-all-button.png';
import claimButton from '../assets/claim-button.png';
import star from '../assets/star.png';
import steak from '../assets/steak.png';
import Gutter from './Gutter';

// TODO: ADD MODAL TO SHOW STAKING TX AND IF USER GOES DOWN A TROPHY TIER

export const StakedTokensContext = createContext()

const Staking = () => {
    const userWallet = useContext(WalletContext);

    const [tokens, setTokens] = useState(null);

    const [showModal, setShowModal] = useState(false);
    const [pendingTx, setPendingTx] = useState(null);

    const [showWarningModal, setShowWarningModal] = useState(false)

    const [activeTokens, setActiveTokens] = useState([]);
    const [stakedMoonrunners, setStakedMoonrunners] = useState([]);
    const [possibleTrophy, setPossibleTrophy] = useState([]);




    const toggleClass = (tokenId) => {
      const newActiveTokens = [...activeTokens];
      if (newActiveTokens.includes(tokenId)) {
        const index = newActiveTokens.indexOf(tokenId);
        newActiveTokens.splice(index, 1);
      } else {
        newActiveTokens.push(tokenId);
      }
      setActiveTokens(newActiveTokens);
    }

    function createTokenObject(tokenId, isStaked){
        const tokenStructure = {
            tokenId : tokenId.toString(),
            isStaked : isStaked
        };
        return tokenStructure; 
    };
        
    async function getTokens() {
        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");

        const erc721Contract = new ethers.Contract(CONTRACT_INFO.moonrunners.contract, CONTRACT_INFO.moonrunners.abi, provider)
        const stakingContract = new ethers.Contract(CONTRACT_INFO.trophies.contract, CONTRACT_INFO.trophies.abi, provider);

        const ownerTokenIds = await erc721Contract.tokensOfOwner(userWallet.address.toString())  
        const stake = await stakingContract.getStake(userWallet.address.toString())
        const stakedTokenIds = stake.tokenIds
       
        const ownerTokens =  ownerTokenIds.map((tokenId) => ({
            tokenId: tokenId.toString(),
            isStaked: false
        }))

        const stakedTokens = stakedTokenIds.map((id) => createTokenObject(id, true))

        
        const allTokens = ownerTokens.concat(stakedTokens)

        setTokens(allTokens)

        const stakedMoonrunners = stakedTokens.map(token => token.tokenId);
        setStakedMoonrunners(stakedMoonrunners);
    }

    async function unstake(tokenids){
        openWarningModal()
        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        const signer = provider.getSigner();

        const stakingContract = new ethers.Contract(CONTRACT_INFO.trophies.contract, CONTRACT_INFO.trophies.abi, provider);

        const contractWithWallet = stakingContract.connect(signer)
        const unstake = await contractWithWallet.unstake(tokenids);

        await unstake.wait();
        await getTokens();
    }

    async function setApproveAll(){    
        openModal();    
        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        const signer = provider.getSigner();

        const erc721Contract = new ethers.Contract(CONTRACT_INFO.moonrunners.contract, CONTRACT_INFO.moonrunners.abi, provider);
        const contractWithWallet = erc721Contract.connect(signer);

        const setApprove = await contractWithWallet.setApprovalForAll(CONTRACT_INFO.trophies.contract, true);
        setPendingTx(setApprove.hash);
        
        await setApprove.wait();
    }
            
    async function stake(tokenIds) {
        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        const signer = provider.getSigner();

        const stakingContract = new ethers.Contract(CONTRACT_INFO.trophies.contract, CONTRACT_INFO.trophies.abi, provider);
        const erc721Contract = new ethers.Contract(CONTRACT_INFO.moonrunners.contract, CONTRACT_INFO.moonrunners.abi, provider);

        const isApproved = await erc721Contract.isApprovedForAll(signer.getAddress(), CONTRACT_INFO.trophies.contract);
       
        if (!isApproved) {
            console.error('Error: ERC-721 contract not approved');
            setApproveAll()

            return;
        }

        const contractWithWallet = stakingContract.connect(signer)
        const stake = await contractWithWallet.stake(tokenIds);
        
        await stake.wait();
    }

    async function checkTrophyClaim(){
        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        const signer = provider.getSigner();

        const stakingContract = new ethers.Contract(CONTRACT_INFO.trophies.contract, CONTRACT_INFO.trophies.abi, provider);
        
        const contractWithWallet = stakingContract.connect(signer)

        const possibileClaim = await contractWithWallet.getPossibleTrophyClaim();

        const trophyNumber = possibileClaim.toNumber()

        setPossibleTrophy(trophyNumber)

    }

    async function claim(){
        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        const signer = provider.getSigner();

        const stakingContract = new ethers.Contract(CONTRACT_INFO.trophies.contract, CONTRACT_INFO.trophies.abi, provider);
        const contractWithWallet = stakingContract.connect(signer)

        await contractWithWallet.claim();
    }


    const openModal = () => {
        setShowModal(true);
    };   
    
    const closeModal = () => {
        setShowModal(false);
        setPendingTx(null);
    };

    const openWarningModal = () => {
        setShowWarningModal(true);
    };

    const closeWarningModal = () => {
        setShowWarningModal(false);
    };
    
    useEffect(() => {
        if (userWallet.address.length > 0)
        getTokens();
        checkTrophyClaim();
    },[userWallet.address]); 
    
    return(
        <>
        <div className='staking-section'>
        <div className="container">
            <div className='staking-body'>
                <div className='container staking-body-container'>
                <div className='columns staking-heading is-vcentered is-centered'>
                    <div className='column is-3 staking-title-column has-text-center'>
                        <p className='staking-title'>
                            Wolf Report
                        </p>
                    </div>

                    <div className='column'>
                        <LoadingBar/>
                    </div>

                    <div className='column is-5 has-text-right'>
                        <br></br>
                        <>
                            <StakedTokensContext.Provider value={stakedMoonrunners.length}>
                                <Trophies/>  
                            </StakedTokensContext.Provider>
                        </>
                    </div>
                </div>


                {userWallet.address.length === 0 &&
                    <div className='columns is-centered has-text-centered'>
                    <div className='column is-4'>
                        <div onClick={userWallet.connect}><img src={connectButton}/> </div>
                    </div>
                </div>
                }
            </div>

            {tokens && tokens.length > 0 
            ?   
            (   
                <>
                <div className='image-container container'>
                    <div className="columns is-centered has-text-centered is-multiline">
                    {tokens.map(token => (
                        <div key={token.tokenId}  className="image-column column is-2 is-vcentered">
                         <img
                            src={'https://moonrunners.herokuapp.com/api/' + token.tokenId + '/image'}
                            className={stakedMoonrunners.includes(token.tokenId) ? (activeTokens.includes(token.tokenId) ? 'token-img-staked-bordered' : 'token-img-staked'): activeTokens.includes(token.tokenId) ? 'token-img-bordered': 'token-img'}
                            onClick={() => toggleClass(token.tokenId)}
                            key={token.tokenId}
                        />
                        {stakedMoonrunners.includes(token.tokenId) ? <div className='staked-text'>Staked <span> <img className='steak-img'src={steak}></img></span></div> 
                        
                        
                        
                        
                        : null}
                      </div>
                       
                    ))}
                    </div> 
                </div> 
                <div className='staking-footer'> 
                    <div className='container action-button-container '> 
                        <div className='columns is-centered is-vcentered is-centered'>
                
                        <div className='column is-1 wof-star-container'>
                                <div ><img className='wof-star' src={star}/> </div>
                            </div>

                                <div className='column'>
                                    <div  onClick={() => stake(activeTokens)}><img className='action-button' src={stakeButton}/> </div>
                                </div>
                                <div className='column'>
                                    <div onClick={() => stake(tokens.map(token => token.tokenId))}><img className='action-button'  src={stakeAllButton}/> </div>
                                </div>
                                <div className='column'>
                                    <div  onClick={() => unstake(activeTokens)}><img className='action-button' src={unstakeButton}/> </div>
                                </div>
                                <div className='column'>
                                    <div onClick={() => unstake(tokens.map(token => token.tokenId))}><img className='action-button' src={unstakeAllButton}/> </div>
                                </div>
                                <div className='column'>
                                    {possibleTrophy <= 0
                                    ?
                                    (
                                        <div  style={{pointerEvents: 'none',
                                        filter: "grayscale(100%)",
                                        }}><img className='action-button' src={claimButton}/> </div>
                                    )
                                    :
                                    (
                                        <div onClick={() => claim()} ><img className='action-button' src={claimButton}/> </div>
                                    )

                                    }
                                    {/* <div onClick={() => claim()} ><img className='action-button' src={claimButton}/> </div> */}

                                </div>

                            <div className='column is-1 wof-star-container'>
                                <div ><img className='wof-star' src={star}/> </div>
                            </div>

                        </div>
                    </div>
                 

                </div> 
                </>
            
            ) 
            :   
            (
                <div className="has-text-centered is-size-3">
                    <h1 className='buy-text'> Buy moonrunners on <a href='https://opensea.io/collection/moonrunnersnft'>Opensea</a></h1>
                </div>
            )
            }
           
            </div>
        </div>
            <div className={`modal ${showModal ? 'is-active' : ''}`} onBlur={closeModal}>
                <div className="modal-background"> </div>
                <div className="modal-card">
                    <header className="modal-card-head">
                        <p className="modal-card-title">NFTs not approved</p>
                        <button className="delete is-large" aria-label="close" onClick={closeModal} />
                    </header>
                    
                    <section className="modal-card-body">
                        <p> Please approve the NFT contract before staking. Follow the metamask directions. </p>
                        {pendingTx != null
                        ?  (
                            <p> You can watch your tx here:  
                            <a href={'https://goerli.etherscan.io/tx/' + pendingTx}> Tx hash </a></p>
                        )
                        :(
                            <p> Your tx hash will appear here once you have approved it on metamask</p> 
                        )
                        }
                    </section>
                </div>
            </div>


            <div className={`modal ${showWarningModal ? 'is-active' : ''}`}>
                <div className="modal-background"> </div>
                <div className="modal-card">
                    <header className="modal-card-head">
                        <p className="modal-card-title">Warning!</p>
                        <button className="delete is-large" aria-label="close" onClick={closeWarningModal} />
                    </header>
                    
                    <section className="modal-card-body">
                        <p> You're about to unstake one or more Moonrunners, by doing so you might lose your trophy status, and you might miss out on utility, value and future airdrops. You will also need to claim your new soulbound trophy if you move to a new tier. Awooooo </p>
                    </section>
                </div>
            </div>



            </div>
            <Gutter/>
        </>              
    )
};
  
export default Staking;